* {
  margin: 0;
  padding: 0;
}
.nav {
  border-bottom: 2px solid rgb(180, 180, 168);
}
.nav,
.logo,
.nav-links {
  display: flex;
  align-items: center;
}
.nav-links,
.logo {
  width: 50%;
}
.nav-links {
  justify-content: space-evenly;
}
.nav-links > a {
  color: white;
  background: rgba(0, 0, 0, 0.863);
  padding: 1% 3%;
  border-radius: 20px;
  font-weight: 400;
  font-size: 18px;
  text-decoration: none;
}
span {
  color: orange;
}
.logo,
.logo img {
  height: 100px;
}
.groceries {
  display: flex;
  flex-wrap: wrap;
  /* margin-left:4%; */
  /* justify-content: space-evenly; */
}
.n1 {
  font-family: "Poppins";
  cursor: pointer;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin: 2% 2%;
  /* border: 1px solid green; */
  min-height: 100px;
  min-width: 200px;
  text-align: center;
}
.n1 img {
  height: 120px;
  padding: 5%;
}
button,
.edit {
  cursor: pointer;
  background-color: orange;
  color: white;
  border: none;
  padding: 2% 3%;
  font-size: 17px;
  font-weight: 400;
  margin: 6% 0%;
  border-radius: 10px;
}
.edit {
  background-color: limegreen;
}
h2,
.container {
  margin: 0% 5%;
}
.form {
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  border-radius: 30px;
  margin: 5% 30%;
  background: white;
}

.form h2 {
  text-align: center;
  color: orange;
  padding-top: 5%;
  font-weight: 700;
  font-size: 30px;
}
input[type="text"] {
  font-family: "Poppins";
  width: 90%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: black;
  margin-top: 10%;
}

input[type="submit"] {
  cursor: pointer;
  background-color: rgb(228, 153, 15);
  color: white;
  margin: 10% 20%;
  padding: 2% 3%;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  width: 50%;
  text-align: center;
}
.bg {
  border: 1px solid yellowgreen;
  background-size: contain;
  height: 100%;
  background-image: url("https://i.pinimg.com/736x/0a/5b/36/0a5b36e385422555e31b734b63cfa1ba.jpg");
}
.fa {
  color: red;
  font-size: 25px;
}
.n1 > input {
  text-align: center;
  border: none;
  font-size: 20px;
  width: 50%;
  color: greenyellow;
  margin: 0%;
  padding: 0;
}
textarea {
  border: none;
  text-align: center;
}